


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
 
}

body {
    background-color: #EDF5E0;
    
}

.heading__container {
    text-align: center;
}
.heading {
    color: #05396B;
    font-size: 30px;
    text-align: center;
    margin: 20px auto;
    display: inline-block;
    padding: 20px;

    position: relative;
}

.heading::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -45%;
    width: 43px;
    height: 3px;
    background: #8DE4AF;

}

.heading::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -45%;
    width: 43px;
    height: 3px;
    background: #8DE4AF;

}

.userContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-bottom: 40px;
    
}

.postsContainer {
    padding: 20px;
    
    display: grid;
    grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 30px;
}




.user {
    width: 32rem;
    background: #5CDB94;
    color: #05396B;
    
    margin: 20px;
    padding: 20px 15px;
    border-radius: 5px;
}

.user h1 {
    color: #EDF5E0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.8rem;
    margin-bottom: 12px;
}

.user h2 {
    margin-bottom: 12px;
}

.user ul {
    list-style: none;
}

.user ul li {
    margin-bottom: 10px;
    display: -webkit-flex;
    display: flex;
    
}

.user i {
    margin-right: 10px;
    color: #389583;
}


.posts__heading {
    color: #A33327;
}

.posts__heading::before,
.posts__heading::after {
    background: #161616;
}

.post {
    background: #151515;
    padding: 20px 15px;
    border-radius: 3px;
    transition: all .3s ease;
}

.post:hover {
    box-shadow: 3px 3px 11px #AD8174, -3px -3px 10px #AD8174;
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
    cursor: pointer;
}

.post h3 {
    color: #C7493A;
    text-transform: uppercase;
    margin-bottom: 10px;

}

.post p {
    color: #689775;
    text-transform: capitalize;

}
